@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
#app {
  background-color: rgba(249, 250, 250, 0.938);
  min-height: 100vh;
  position: relative;
  font-family: "Montserrat", sans-serif;
}

footer {
  position: absolute;
  bottom: 0;
  font-size: 1.2em;
  background-color: #4285f4;
  color: #fff;
  width: 100vw;
  text-align: center;
  padding: 5px;
}

#to-do-items{
  -webkit-flex:1 1;
          flex:1 1;
  display:-webkit-flex;
  display:flex;
}

.todo-details{
 width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.todo-item {
  font-size: 1.2em;
}

.date-time {
  font-size: 0.8em;
}

.date-action-container{
  display: -webkit-flex;
  display: flex;
  padding:2%;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-self:center;
          align-self:center;
}

.delete-btn {
  cursor: pointer;
}

form {
  display: -webkit-flex;
  display: flex;
}


@media (min-width: 700px) {
  form {
    width: 40vw;
  }

  input {
    width: 90% !important;
  }
}

@media (max-width: 699px) {
  form {
    width: 85vw;
  }

  input {
    width: 80% !important;
  }
}

.collection-header {
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #4285f4 !important;
  color: #fff;
}
.todo-input-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-bottom: 2.5rem;
}

.collection {
  overflow-wrap: break-word;
}

.collection-item{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}

.input-field{
  border-top: 2px solid lightgrey;
}

.drag-icon-container{
  margin-right: 3%;
  margin-left: -1%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding:0.5%;
  /* margin-top: 2%; */
}

.drag-icon{
  height: 100%;
  width: 100%;
}

@media (min-width: 700px) {
  .collection {
    min-width: 40vw;
  }

  .drag-icon-container{
    margin-left: -1%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding:0.5%;
    /* margin-top: 2%; */
  }
  
  .drag-icon{
    height: 100%;
    width: 100%;
  }
}

@media (max-width: 699px) {
  .collection {
    width: 95vw;
  }
  .drag-icon-container{
    margin-left: -5%;
  padding:5%;

  }
}

